<template>
    <div class="article-list">
        <ul class="top">
            <li class="item" v-for="(art, index) in lists" :key="index" @click="readArt(index)">
                <span class="iconfont icon-taoxin"></span>
                <p class="title">{{art.title}}</p>
                <p class="time">{{art.create_time | date}}</p>
            </li>
        </ul>
        <div class="bottom">
            <p @click="getPage(-1)">上一页</p>
            <p>第 {{page}} 页</p>
            <p @click="getPage(1)">下一页</p>
        </div>
    </div>
</template>
<script>
import { getArticleList } from '@/api'
import { articleMixin } from 'common/js/mixin'
export default {
    mixins: [articleMixin],
    data () {
        return {
            lists: [],
            page: 1,
            ismore: true,
            size: 20,
            totalPage: 1,
            cateid: 0,
            cateindex: 0
        }
    },
    mounted() {
        let { cateid, cateindex } = this.$route.query
        this.cateid = cateid
        this.cateindex = cateindex
        this.getArtList(cateid, cateindex)
    },
    methods: {
        readArt (index) {
            // 转到列表阅读
            this.set_articleList(this.lists)
            this.set_articleIndex(index)
            this.$router.push({ path: '/artreader' })
        },
        async getArtList (cateid, cateindex, page) {
            this.lists = await getArticleList(cateid, cateindex, page)
            if (this.lists.length < 20) {
                this.ismore = false
            } else {
                this.ismore = true
            }
        },
        getPage (n) {
            if (n === 1 && this.ismore) {
                this.page++
            }
            if (n === -1) {
                this.page = this.page - 1 > 1 ? this.page - 1 : 1
            }
        }
    },
    watch: {
        page (newpage) {
            this.getArtList(this.cateid, this.cateindex, newpage)
        }
    },
    filters: {
        date (t) {
            t = new Date(Number(t))
            let reg = /\//g
            return t.toLocaleDateString().replace(reg, '-')
        },
        time (t) {
            t = new Date(Number(t))
            let reg = /[:|\d]+/
            let res
            t.toTimeString().replace(reg, $1 => {
                res = $1
            })
            return res
        }
    }
}
</script>
<style lang="less" scoped>
.article-list{
    min-height: 550px;
    padding: 50px 0;
    width: 100%;
    position: relative;
    .top{
        width: 100%;
        .item{
            width: 47%;
            padding: 13px 0 13px 10px;
            display: flex;
            &:nth-of-type(2n - 1) {
                float: left;
            }
            &:nth-of-type(2n) {
                float: right;
            }
            &::after{
                content: '';
                display: block;
                clear: both;
            }
            .iconfont{
                line-height: 25px;
                color: #f59cab;
            }
            .title{
                flex-grow: 1;
                text-align: left;
                padding: 0 30px 0 10px;
                cursor: pointer;
                overflow: hidden;
                text-overflow: ellipsis;
                white-space: nowrap;
            }
            .time{
                flex: 0 0 85px;
                line-height: 24px;
            }
        }
    }
    .bottom{
        position: absolute;
        width: 100%;
        bottom: 0;
        left: 0;
        display: flex;
        justify-content: space-around;
        p{
            padding: 10px 0;
            line-height: 25px;
            color: #e0c073;
            cursor: pointer;
        }
    }
}
@media screen and(max-width: 700px) {
    .article-list{
        .top{
            .item{
                box-sizing: border-box;
                width: 100%;
                padding: 8px 15px;
            }
        }
    }
}
</style>



